import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AppAlertService } from '@shared/service/app/alert.service';
import { CobranzasService } from '../../services/cobranzas.service';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { Subject, takeUntil } from 'rxjs';
import { environment } from 'environments/environment';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';

@Component({
    selector: 'app-cobranzas',
    templateUrl: './cobranzas.component.html',
})
export class CobranzasComponent implements OnInit, OnDestroy {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    private token = localStorage.getItem('accessToken');

    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    hasData;

    headers = ['Fecha', 'Cliente', 'Neto a Descontar ', 'Pago Real (Bruto)', 'A Facturar', '', ''];
    column_params = ['fecha', 'descripcion_cliente', 'importe_neto', 'importe_real', 'a_facturar', 'facturado', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = [];
    filter;
    @Input() extraParams = '';
    env = environment.ventas.view_vta_cobros + "?";

    constructor(
        private alert: AppAlertService,
        protected router: Router,
        private confirm: FuseConfirmationService,
        private _cobranzasService: CobranzasService,
        public _headerTextService: HeaderTextService,
        private _empresaService: EmpresaService,
    ) {
        this._headerTextService.setTitulo(this.router);
    }
    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {

        /* this.checkTabla() */

        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        this.funcionesObjeto = [
            {
                nombre_boton: "editar",
                functionName: 'editar',
                iconoAccion: () => 'jedstion:editar',
                iconoAccionado: () => '' /* 'jedstion:editar_accionado' */,
                iconoDeshabilitado: () => '' /* 'jedstion:editar_disabled' */,
            },
            {
                nombre_boton: "eliminar",
                functionName: 'eliminar',
                iconoAccion: () => 'jedstion:eliminar',
                iconoAccionado: () => '' /* 'jedstion:eliminar_accionado' */,
                iconoDeshabilitado: () => '' /* 'jedstion:eliminar_disabled' */,
            },
            {
                nombre_boton: "Reporte",
                functionName: 'generar_reporte',
                iconoAccion: () => 'jedstion:imprimir',
                iconoAccionado: () => '' /* 'jedstion:imprimir_accionado' */,
                iconoDeshabilitado: () => '' /* 'jedstion:imprimir_disabled' */,
            },
            /* {
                nombre_boton: "Guardar Factura",
                functionName: 'guardar_factura',
                iconoAccion: () => 'jedstion:reporte_de_venta',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
            }, */
        ];
    }

    /* checkTabla(){
        console.log(this.tabla)
        this.hasData = this.tabla !== undefined ? true : false;
    } */


    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateCobranza(event);
                break;
            case 'eliminar':
                this.deleteCobranza(event);
                break;
            case 'generar_reporte':
                this.generarReporteCobro(event);
                break;
            /* case 'guardar_factura':
                this.guardarFactura(event);
                break; */
        }
    }

    deleteCobranza(event: { name: string, params: any }) {
        const idcobro = event.params.data.idcobro;
        this.confirm.open({
            title: "Eliminar Cobranza: " + idcobro,
            message: `Desea eliminar la Cobranza: ${event.params.data.idcobro} -  ${event.params.data.descripcion_cliente}?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._cobranzasService.deleteCobranza(this.token, idcobro).subscribe({
                        next: (res) => {
                            this.alert.success("Cobranza elimada con xito");
                            setTimeout(() => {
                                this.tabla.renderTabla();
                                // this.checkTabla();
                            }, 750)
                        },
                        error: (err) => {
                            this.alert.error("Ocurri un error al eliminar la cobranza");
                        }
                    })
                }
            });
    }

    updateCobranza(event: { name: string, params: any }) {
        const id = event.params.data.idcobro
        this.router.navigate([`/cobranzas/edicion-cobro/${id}`]);
    }

    onNuevaCobranza() {
        this.router.navigate(['/cobranzas/alta-cobro'])
    }

    generarReporteCobro(event) {

        const params = `?idcobro=${event.params.data.idcobro}`

        this._cobranzasService.generarReporteCobro(this.token, params).subscribe({
            next: (res: any) => {
                const path = res.path
                window.open(environment.baseRest + "/" + path, '_blank')
            },
            error: (err) => {
                this.alert.error("Ocurri un error al generar el reporte")
            }
        })
    }

    /* guardarFactura(event){
        this.router.navigate(['/cobranzas/guardar-factura']);
    } */
    ngOnDestroy(): void { }
}
