import { RouterModule, Routes } from '@angular/router';
import { CommonModule, DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatTableModule } from '@angular/material/table';
import { MatButtonModule } from '@angular/material/button';
import { MatSelectModule } from '@angular/material/select';
import { MatFormFieldModule } from '@angular/material/form-field';
import { ReactiveFormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { FuseAlertComponent } from '@fuse/components/alert';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MatIconModule } from '@angular/material/icon';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSortModule } from '@angular/material/sort';
import { TablaDinamicaModule } from '@shared/components/tabla-dinamica/tabla-dinamica.module';
import { MatDialogModule } from '@angular/material/dialog';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { CobranzasService } from './services/cobranzas.service';
import { CobranzasComponent } from './components/cobranzas/cobranzas.component';
import { AltaEdicionCobroComponent } from './components/alta-edicion-cobro/alta-edicion-cobro.component';
//import { GuardarFacturaComponent } from './components/guardar-factura/guardar-factura.component';
const routes: Routes = [
    {
        path: '',
        component: CobranzasComponent,
    },
    {
        path: 'alta-cobro',
        component: AltaEdicionCobroComponent,
    },
    {
        path: 'edicion-cobro/:idcobro',
        component: AltaEdicionCobroComponent,
    },
    /* {
        path: 'guardar-factura',
        component: GuardarFacturaComponent,
    }, */
];
@NgModule({
    declarations: [
        CobranzasComponent,
        AltaEdicionCobroComponent,
        /* GuardarFacturaComponent, */
    ],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        MatTableModule,
        MatButtonModule,
        MatSelectModule,
        MatFormFieldModule,
        ReactiveFormsModule,
        MatInputModule,
        MatIconModule,
        FuseAlertComponent,
        MatTooltipModule,
        MatAutocompleteModule,
        MatToolbarModule,
        MatCheckboxModule,
        MatSortModule,
        TablaDinamicaModule,
        MatDialogModule,
        MatDatepickerModule,
    ],
    exports: [RouterModule],
    providers: [
        JedsMenuService,
        AppAlertService,
        DatePipe,
        FuseConfirmationService,
        CobranzasService,
    ],
})
export class CobranzasModule {}
