import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AppAlertService } from '@shared/service/app/alert.service';
import { CobranzasService } from '../../services/cobranzas.service';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { debounceTime, distinctUntilChanged, Subject, takeUntil } from 'rxjs';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { MatTableDataSource } from '@angular/material/table';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { set } from 'lodash';

@Component({
    selector: 'app-alta-edicion-cobro',
    templateUrl: './alta-edicion-cobro.component.html',
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }],
})
export class AltaEdicionCobroComponent implements OnInit, OnDestroy {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    private token = localStorage.getItem('accessToken');

    totalPagar;
    saldoPagar;
    esEdicion;
    idItemEdicion;

    clientes;
    bancos;
    metodosCobro;
    ventasPendientes;

    ultimoModificado;
    pagoRealSub;
    netoDescontarSub;
    formaCobroSub;
    asignacionSub;
    fechaSub;

    lleva_numero;
    lleva_vencimiento;
    lleva_fecha_emision;
    lleva_fecha_cobro;
    lleva_banco;

    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';

    formAsignacion: FormGroup;
    formFormaCobro: FormGroup;

    dataSourceAsignacion = new MatTableDataSource();
    asignacionColumns = [
        'descripcion',
        'importe_neto',
        'saldo',
        'interes',
        'asignacion',
        'importe_total',
    ];

    dataSourceFormaPago = new MatTableDataSource();
    formaCobroColumns = [
        'descripcion',
        'numero_comprobante',
        'fecha_emision',
        'fecha_cobro',
        'fecha_vencimiento',
        'banco',
        'importe_neto',
        'banco',
        'importe_interes_descuento',
        'importe_real',
    ];

    constructor(
        private alert: AppAlertService,
        protected router: Router,
        private _cobranzasService: CobranzasService,
        public _headerTextService: HeaderTextService,
        private _empresaService: EmpresaService
    ) {
        this._headerTextService.setTitulo(this.router);
    }
    ngOnInit(): void {
        this.esEdicion = this.router.url.includes('edicion-cobro') ? true : false;
        
        
        this._empresaService.empresa$
        .pipe(takeUntil(this._unsubscribeAll))
        .subscribe((empresa) => {
            this.color_primario = empresa.color_primario;
            this.color_secundario = empresa.color_secundario;
        });
        
        if(this.esEdicion){
            this.idItemEdicion = this.router.url.split('/')[3];
            this.getItemEdicion(this.idItemEdicion);
        }
        this.newFormAsignacion();
        this.newFormFormaCobro();

        this.getBancos();
        this.getMetodosCobro();

        /* Funciones para manejar los eventos de los campos Neto a Descontar y Pago Real -------------> */
        this.formaCobroSub = this.formFormaCobro
            .get('formaCobro')
            ?.valueChanges.subscribe((value) => {
                this.enableDisableControls(value);
                this.calcularValores();
            });

        this.netoDescontarSub = this.formFormaCobro
            .get('netoDescontar')
            ?.valueChanges.subscribe(() => {
                this.ultimoModificado = 'netoDescontar';
                if (this.formFormaCobro.get('netoDescontar')?.value) {
                    this.formFormaCobro
                        .get('pagoReal')
                        .disable({ emitEvent: false });
                } else {
                    this.formFormaCobro
                        .get('pagoReal')
                        .enable({ emitEvent: false });
                }

                setTimeout(() => {
                    this.calcularValores();
                }, 750);
            });

        this.pagoRealSub = this.formFormaCobro
            .get('pagoReal')
            ?.valueChanges.subscribe(() => {
                this.ultimoModificado = 'pagoReal';
                if (this.formFormaCobro.get('pagoReal')?.value) {
                    this.formFormaCobro
                        .get('netoDescontar')
                        .disable({ emitEvent: false });
                } else {
                    this.formFormaCobro
                        .get('netoDescontar')
                        .enable({ emitEvent: false });
                }

                setTimeout(() => {
                    this.calcularValores();
                }, 750);
            });
        /* -------------------------------------------------------------------------------------------> */

        this.asignacionSub = this.formAsignacion
            .get('asignacion')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(750))
            .subscribe((value) => {
                this.calcularInteres();
            });

        this.fechaSub = this.formAsignacion
            .get('fecha')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(300))
            .subscribe((value) => {
                this.calcularInteres();
            });

        this.formAsignacion
            .get('cliente')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(300))
            .subscribe({
                next: (value) => this.getClientes(value),
                error: (err) =>
                    console.error('Error al obtener cambios de cliente:', err),
            });
    }

    displayFn(cliente): string {
        return cliente && cliente.razonSocial ? cliente.razonSocial : '';
    }

    getItemEdicion(idItemEdicion){
        let params = `?idcobro=${idItemEdicion}`;

        this._cobranzasService.getItemEdicion(this.token, params).subscribe({
            next: (data: any) => {
                this.dataSourceAsignacion.data = data.result.items_cobro_asignacion;
                this.totalPagar = this.dataSourceAsignacion.data.reduce(
                    (acum, item: any) => acum + item.importe_total,
                    0
                );

                this.formFormaCobro
                .get('netoDescontar')
                .setValue(this.totalPagar);
                this.formFormaCobro
                .get('netoDescontar')
                .setValidators([
                    Validators.required,
                    Validators.max(this.totalPagar),
                ]);
                this.formFormaCobro
                    .get('netoDescontar')
                    .updateValueAndValidity({ emitEvent: false });

                this.dataSourceFormaPago.data = data.result.items_forma_pago;
                this.saldoPagar = this.dataSourceFormaPago.data.reduce(
                    (acum, item: any) => acum + item.importe_real,
                    0
                );
            },
            error: (err) => {
                console.error('Error al obtener item de edicion:', err);
            },
        });
    }

    newFormAsignacion() {
        this.formAsignacion = new FormGroup({
            fecha: new FormControl(new Date(), [Validators.required]),
            cliente: new FormControl('', [Validators.required]),
            bonificada: new FormControl(false, [Validators.required]),
            venta: new FormControl('', [Validators.required]),
            pagoParcial: new FormControl(false, [Validators.required]),
            asignacion: new FormControl('', [Validators.required]),
            interesMora: new FormControl({ value: 0, disabled: true }, [
                Validators.required,
            ]),
        });
    }

    newFormFormaCobro() {
        this.formFormaCobro = new FormGroup({
            formaCobro: new FormControl('', [Validators.required]),
            netoDescontar: new FormControl('', [Validators.required]),
            pagoReal: new FormControl('', [Validators.required]),
            numero: new FormControl(null),
            emision: new FormControl(null),
            cobro: new FormControl(null),
            vencimiento: new FormControl(null),
            banco: new FormControl(null),
        });
    }

    getClientes(value: string): void {
        const filter = value ? `?filter[razonSocial][like]=${value}` : '';
        this._cobranzasService.getClientes(this.token, filter).subscribe({
            next: (data: any) => {
                this.clientes = data.result;
            },
            error: (err) => {
                console.error('Error al obtener clientes:', err);
            },
        });
    }

    getVentaPendiente() {
        let filter = `?filter[idcliente]=${
            this.formAsignacion.get('cliente').value.codigo
        }&filter[bonificado]=${
            this.formAsignacion.get('bonificada').value ? 1 : 0
        }&filter[saldo][gt]=0`;

        this._cobranzasService.getVentaPendiente(this.token, filter).subscribe({
            next: (data: any) => {
                this.ventasPendientes = data.result;
            },
            error: (err) => {
                console.error('Error al obtener ventas pendientes:', err);
            },
        });
    }

    getBancos() {
        this._cobranzasService.getBancos(this.token).subscribe({
            next: (data: any) => {
                this.bancos = data;
            },
            error: (err) => {
                console.error('Error al obtener bancos:', err);
            },
        });
    }

    getMetodosCobro() {
        this._cobranzasService.getMetodosCobro(this.token).subscribe({
            next: (data: any) => {
                this.metodosCobro = data.result;
            },
            error: (err) => {
                console.error('Error al obtener metodos de pago:', err);
            },
        });
    }

    calcularInteres() {
        let venta = this.formAsignacion.get('venta').value
            ? this.formAsignacion.get('venta').value
            : null;
        let asignacion = this.formAsignacion.get('asignacion').value
            ? this.formAsignacion.get('asignacion').value
            : null;
        let fecha = this.formAsignacion.get('fecha').value
            ? new Date(this.formAsignacion.get('fecha').value)
            : null;

        let fechaFormat = `${fecha.getFullYear()}-${
            fecha.getMonth() + 1
        }-${fecha.getDate()} ${fecha.getHours()}:${fecha.getMinutes()}:${fecha.getSeconds()}`;

        if (venta.asignacion === 0) {
            if (this.formAsignacion.get('pagoParcial').value) {
                // No lleva calculo de interes y deja al usaurio setear la asignacion
                // ya que es un pago parcial
                this.formAsignacion
                    .get('asignacion')
                    .enable({ emitEvent: false });
                this.formAsignacion
                    .get('interesMora')
                    .setValue(0, { emitEvent: false });
                this.formAsignacion
                    .get('asignacion')
                    .setValidators([
                        Validators.required,
                        Validators.max(venta.saldo),
                    ]);
                this.formAsignacion
                    .get('asignacion')
                    .updateValueAndValidity({ emitEvent: false });
            } else {
                // No lleva calculo de interes e inhabilita el campo asignacion
                // seteandole el valor del saldo de la venta ya que es un pago total
                this.formAsignacion
                    .get('asignacion')
                    .disable({ emitEvent: false });
                this.formAsignacion
                    .get('interesMora')
                    .setValue(0, { emitEvent: false });
                this.formAsignacion
                    .get('asignacion')
                    .setValue(venta.saldo, { emitEvent: false });
                this.formAsignacion
                    .get('asignacion')
                    .setValidators([
                        Validators.required,
                        Validators.max(venta.saldo),
                    ]);
                this.formAsignacion
                    .get('asignacion')
                    .updateValueAndValidity({ emitEvent: false });
            }
        } else {
            if (this.formAsignacion.get('pagoParcial').value) {
                // Se va a calcular el interes por mora ya que es un pagar
                // y le permite al usuario ingresar la asignacion ya que es un pago parcial
                console.log(venta.saldo);
                this.formAsignacion
                    .get('asignacion')
                    .enable({ emitEvent: false });
                this.formAsignacion
                    .get('asignacion')
                    .setValidators([
                        Validators.required,
                        Validators.max(venta.saldo),
                    ]);
                this.formAsignacion
                    .get('asignacion')
                    .updateValueAndValidity({ emitEvent: false });
            } else {
                // Se va a calcular el interes por mora ya que es un pagar pero no se
                // le va a permitir al usuario ingresar la asignacion porque es un pago total
                this.formAsignacion
                    .get('asignacion')
                    .disable({ emitEvent: false });
                this.formAsignacion
                    .get('asignacion')
                    .setValue(venta.saldo, { emitEvent: true });
                this.formAsignacion
                    .get('asignacion')
                    .setValidators([
                        Validators.required,
                        Validators.max(venta.saldo),
                    ]);
                this.formAsignacion
                    .get('asignacion')
                    .updateValueAndValidity({ emitEvent: false });
            }

            // Calculo del interes por mora --------------------->
            if (venta.id && asignacion && fecha) {
                let payload = {
                    id: venta.id,
                    asignacion: asignacion,
                    fecha: fechaFormat,
                };

                this._cobranzasService
                    .calcularInteres(this.token, payload)
                    .subscribe({
                        next: (data: any) => {
                            this.formAsignacion
                                .get('interesMora')
                                .setValue(data.result.interes, {
                                    emitEvent: false,
                                });
                        },
                        error: (err) => {
                            console.error('Error al calcular interes:', err);
                        },
                    });
            } else {
                this.alert.warning(
                    'Debe completar los campos para calcular el inters'
                );
            }
        }
    }

    calcularValores() {
        if (this.formFormaCobro.get(this.ultimoModificado)?.value) {
            if (this.formFormaCobro.get('formaCobro').value) {
                if (
                    this.formFormaCobro.get('netoDescontar').value ||
                    this.formFormaCobro.get('pagoReal').value
                ) {
                    let payload = {
                        idcobrotipo:
                            this.formFormaCobro.get('formaCobro').value,
                        importe_pago_real:
                            this.ultimoModificado === 'pagoReal'
                                ? this.formFormaCobro.get('pagoReal').value
                                : null,
                        importe_neto_a_descontar:
                            this.ultimoModificado === 'netoDescontar'
                                ? this.formFormaCobro.get('netoDescontar').value
                                : null,
                    };

                    this._cobranzasService
                        .calcularImporte(this.token, payload)
                        .subscribe({
                            next: (res: any) => {
                                if (res.result) {
                                    let netoDescontar = res.result
                                        .importe_neto_a_descontar
                                        ? res.result.importe_neto_a_descontar
                                        : null;
                                    let pagoReal = res.result.importe_pago_real
                                        ? res.result.importe_pago_real
                                        : null;

                                    netoDescontar
                                        ? this.formFormaCobro
                                              .get('netoDescontar')
                                              .setValue(netoDescontar, {
                                                  emitEvent: false,
                                              })
                                        : '';

                                    pagoReal
                                        ? this.formFormaCobro
                                              .get('pagoReal')
                                              .setValue(pagoReal, {
                                                  emitEvent: false,
                                              })
                                        : '';

                                    this.formFormaCobro
                                        .get('pagoReal')
                                        .enable({ emitEvent: false });
                                    this.formFormaCobro
                                        .get('netoDescontar')
                                        .enable({ emitEvent: false });
                                }
                            },
                            error: (err) => {
                                console.error(err);
                            },
                        });
                } else {
                    this.alert.warning('Debe ingresar un valor');
                }
            } else {
                this.alert.warning('Debe seleccionar un Mtodo de Cobro');
            }
        } else {
            this.alert.warning('Debe ingresar un valor al modificar');
        }
    }

    async enableDisableControls(metodo) {

        this.lleva_numero = (await metodo.lleva_numero) === 1 ? true : false;
        metodo.lleva_numero === 1
            ? this.formFormaCobro.get('numero').enable()
            : this.formFormaCobro.get('numero').disable(), this.formFormaCobro.get('numero').setValue(null, { emitEvent: false });

        this.lleva_vencimiento =
            (await metodo.lleva_vencimiento) === 1 ? true : false;
        metodo.lleva_vencimiento === 1
            ? this.formFormaCobro.get('vencimiento').enable()
            : this.formFormaCobro.get('vencimiento').disable(), this.formFormaCobro.get('vencimiento').setValue(null, { emitEvent: false });

        this.lleva_fecha_emision =
            (await metodo.lleva_fecha_emision) === 1 ? true : false;
        metodo.lleva_fecha_emision === 1
            ? this.formFormaCobro.get('emision').enable()
            : this.formFormaCobro.get('emision').disable(), this.formFormaCobro.get('emision').setValue(null, { emitEvent: false });

        this.lleva_fecha_cobro =
            (await metodo.lleva_fecha_cobro) === 1 ? true : false;
        metodo.lleva_fecha_cobro === 1
            ? this.formFormaCobro.get('cobro').enable()
            : this.formFormaCobro.get('cobro').disable(), this.formFormaCobro.get('cobro').setValue(null, { emitEvent: false });

        this.lleva_banco = (await metodo.lleva_banco) === 1 ? true : false;
        metodo.lleva_banco === 1
            ? this.formFormaCobro.get('banco').enable()
            : this.formFormaCobro.get('banco').disable(), this.formFormaCobro.get('banco').setValue(null, { emitEvent: false });
    }

    addAsignacion() {
        if (this.formAsignacion.valid) {
            let payload = {
                id: this.formAsignacion.get('venta').value.id,
                es_pago_parcial: this.formAsignacion.get('pagoParcial').value
                    ? 1
                    : 0,
                asignacion: this.formAsignacion.get('asignacion').value,
                interes: this.formAsignacion.get('interesMora').value,
            };
            this._cobranzasService
                .cargarItemCobro(this.token, payload)
                .subscribe({
                    next: (data: any) => {
                        this.dataSourceAsignacion.data = [
                            ...this.dataSourceAsignacion.data,
                            data.result,
                        ];
                        this.totalPagar = this.dataSourceAsignacion.data.reduce(
                            (acum, item: any) => acum + item.importe_total,
                            0
                        );

                        this.formFormaCobro
                        .get('netoDescontar')
                        .setValue(this.totalPagar);
                        this.formFormaCobro
                        .get('netoDescontar')
                        .setValidators([
                            Validators.required,
                            Validators.max(this.totalPagar),
                        ]);
                        this.formFormaCobro
                            .get('netoDescontar')
                            .updateValueAndValidity({ emitEvent: false });
                            },

                    error: (err) => {
                        console.error('Error al cargar asignacion:', err);
                    },
                });
        } else {
            this.alert.warning('Revise los campos y vuelva a intentar');
        }
    }

    addFormaCobro() {
        let fecha_emision = this.formFormaCobro.get('emision').value
            ? new Date(this.formFormaCobro.get('emision').value)
            : null;
        let fecha_emision_format = fecha_emision
            ? `${fecha_emision.getFullYear()}-${
                  fecha_emision.getMonth() + 1
              }-${fecha_emision.getDate()}`
            : null;

        let fecha_cobro = this.formFormaCobro.get('cobro').value
            ? new Date(this.formFormaCobro.get('cobro').value)
            : null;
        let fecha_cobro_format = fecha_cobro
            ? `${fecha_cobro.getFullYear()}-${
                  fecha_cobro.getMonth() + 1
              }-${fecha_cobro.getDate()}`
            : null;

        let fecha_vencimiento = this.formFormaCobro.get('vencimiento').value
            ? new Date(this.formFormaCobro.get('vencimiento').value)
            : null;
        let fecha_vencimiento_format = fecha_vencimiento
            ? `${fecha_vencimiento.getFullYear()}-${
                  fecha_vencimiento.getMonth() + 1
              }-${fecha_vencimiento.getDate()}`
            : null;

        let payload = {
            idcobrotipo:
                this.formFormaCobro.get('formaCobro').value.idcobrotipo,
            importe_real:
                this.ultimoModificado === 'pagoReal'
                    ? this.formFormaCobro.get('pagoReal').value
                        ? this.formFormaCobro.get('pagoReal').value
                        : null
                    : null, // nulleable. Si envias este campo, debes enviar importe_neto en null
            importe_neto:
                this.ultimoModificado === 'netoDescontar'
                    ? this.formFormaCobro.get('netoDescontar').value
                        ? this.formFormaCobro.get('netoDescontar').value
                        : null
                    : null, // nulleable. Si envias este campo, debes enviar importe_real en null
            numero_comprobante: this.formFormaCobro.get('numero').value
                ? this.formFormaCobro.get('numero').value
                : null, // nulleable
            fecha_emision: fecha_emision_format, // nulleable
            fecha_cobro: fecha_cobro_format, // nulleable
            fecha_vencimiento: fecha_vencimiento_format, // nulleable
            idbanco: this.formFormaCobro.get('banco').value, // nulleable
        };

        if (this.lleva_banco === false) {
            delete payload.idbanco;
        }
        if (this.lleva_numero === false) {
            delete payload.numero_comprobante;
        }
        if (this.lleva_fecha_emision === false) {
            delete payload.fecha_emision;
        }
        if (this.lleva_fecha_cobro === false) {
            delete payload.fecha_cobro;
        }
        if (this.lleva_vencimiento === false) {
            delete payload.fecha_vencimiento;
        }

        this._cobranzasService
            .cargarItemFormaCobro(this.token, payload)
            .subscribe({
                next: (data: any) => {
                    this.dataSourceFormaPago.data = [
                        ...this.dataSourceFormaPago.data,
                        data.result,
                    ];
                    this.saldoPagar = this.dataSourceFormaPago.data.reduce(
                        (acum, item: any) => acum + item.importe_real,
                        0
                    );
                    console.log('saldoPagar: ', this.saldoPagar);
                },
                error: (err) => {
                    console.error('Error al cargar forma de cobro:', err);
                },
            });
    }

    onSubmit() {
        let fechaCobro = this.formAsignacion.get('fecha').value
            ? new Date(this.formAsignacion.get('fecha').value)
            : null;
        let fechaCobroFormat = fechaCobro
            ? `${fechaCobro.getFullYear()}-${
                  fechaCobro.getMonth() + 1
              }-${fechaCobro.getDate()} ${fechaCobro.getHours()}:${fechaCobro.getMinutes()}:${fechaCobro.getSeconds()}`
            : null;

        let payload = {
            cobro: {
                fecha: fechaCobroFormat,
                idcliente: this.formAsignacion.get('cliente').value.codigo,
                bonificado: this.formAsignacion.get('bonificada').value === true ? 1 : 0,
                importe_neto: this.formFormaCobro.get('netoDescontar').value,
                importe_real: this.formFormaCobro.get('pagoReal').value,
                intereses: this.formAsignacion.get('interesMora').value,
            },
            items_cobro_asignacion: this.dataSourceAsignacion.data.map(
                ({ id, interes, asignacion, operacion_tipo, bonificado }) => ({
                    id,
                    interes,
                    asignacion,
                    operacion_tipo,
                    bonificado: bonificado === true ? 1 : 0,
                })
            ),
            items_forma_pago: this.dataSourceFormaPago.data.map(
                ({
                    idcobrotipo,
                    importe_neto,
                    importe_real,
                    numero_comprobante,
                    fecha_emision,
                    fecha_cobro,
                    fecha_vencimiento,
                    idbanco,
                }) => ({
                    idcobrotipo,
                    importe_neto,
                    importe_real,
                    numero_comprobante,
                    fecha_emision,
                    fecha_cobro,
                    fecha_vencimiento,
                    idbanco,
                })
            ),
        };

        if(this.esEdicion){
            const idcobro = this.idItemEdicion;
            //Edicion de cobranza
            this._cobranzasService.editarCobranza(this.token, idcobro, payload).subscribe({
                next: (data: any) => {
                    this.alert.success(data.mensaje);
                    this.router.navigate(['/cobranzas']);
                },
                error: (err) => {
                    console.error('Error al guardar cobro:', err);
                },
            });
        }else{
            //Alta de cobranza
            this._cobranzasService.nuevaCobranza(this.token, payload).subscribe({
                next: (data: any) => {
                    this.alert.success(data.mensaje);
                    this.router.navigate(['/cobranzas']);
                },
                error: (err) => {
                    console.error('Error al guardar cobro:', err);
                },
            });
        }
    }

        


    ngOnDestroy(): void {}
}
